/* CSS copied from Ryan Johnson's project: https://codesandbox.io/s/2vyp6kxzvy */
.jokes-list {
    margin-top: 2rem;
    text-align: left;
    list-style: none;
}

.jokes-list > li {
    padding: 1rem;
    margin: 0;
}

.jokes-list > li:nth-child(odd) {
    background-color: rgba(0, 0, 0, 0.16);
}

.jokes-list > li:nth-child(even) {
    background-color: rgba(0, 0, 0, 0.01);
}
